import React, { useCallback, useEffect, useState } from "react";
import { Tooltip as ReactTooltip } from 'react-tooltip';
import { Arrow } from "./Arrow";

const data = {
  timer: (
    <>
      <p>Voc est jogando um jogo que tem um relgio que comea a contar do zero e vai at 60 segundos, o que  o mesmo que dizer um minuto.</p>
      <p>Durante esse tempo, voc pode tentar adivinhar quantas vezes quiser.</p>
      <p>Mas, quando o tempo acabar e o relgio chegar a 60 segundos, no ser mais possvel fazer nenhum palpite.</p>
      <p>Nesse momento, voc ir para o prximo nvel do jogo.</p>
      <p>Ento,  importante prestar ateno no tempo que falta para que voc possa tentar adivinhar o mximo possvel antes que ele acabe!</p>
    </>
  ),
  tips: (
    <>
      <p>Com esse boto especial aparecem algumas informaes que podem ajudar a descobrir em qual pas voc est.</p>
      <p>Essas informaes so chamadas de dicas, e tem um total de 10 delas. Mas, voc s pode escolher 4 dessas dicas para usar e tentar adivinhar o pas certo.</p>
      <p>Ento, preste ateno em qual dica escolher para ter mais chances de acertar!</p>
    </>
  ),
  guess: (
    <>
      <p>Quando estiver se sentindo confiante, quando apertar esse boto um mapa aparecer na tela.</p>
      <p>Voc pode usar esse mapa para escolher em qual pas voc acha que est.</p>
    </>
  ),
  wrongGuess: (
    <>
      <p>Se voc escolher o pas errado no jogo, o pas escolhido ficar marcado em vermelho no mapa.</p>
      <p>Isso significa que voc precisa tentar novamente para escolher o pas certo.</p>
      <p> uma forma de te ajudar a saber que precisa continuar tentando para adivinhar o pas correto.</p>
    </>
  ),
  rightGuess: (
    <>
      <p>Quando voc conseguir adivinhar o pas correto no jogo, uma tela especial ir aparecer na tela com uma mensagem de parabns!</p>
      <p>Nessa tela, voc ir ver as informaes do pas que acertou, como o nome, a bandeira e outros detalhes.</p>
      <p>Alm disso, voc tambm ver quanto tempo demorou para acertar o pas, o que pode ser uma forma divertida de tentar superar todos os recordes!</p>
    </>
  ),
}

export const TutorialContext = React.createContext();
export const Tutorial = ({ active, children }) => {
  const [steps, setSteps] = useState({
    navigation: { completed: false, element: null, tooltip: false },
    timer: { completed: false, element: null, tooltip: true, canLearn: true },
    tips: { completed: false, element: null, tooltip: true, canLearn: true },
    guess: { completed: false, element: null, tooltip: true, canLearn: true },
    wrongGuess: { completed: false, element: null, tooltip: true, canLearn: false },
    rightGuess: { completed: false, element: null, tooltip: true, canLearn: false },
  });
  const [currentStep, setCurrentStep] = useState('navigation');

  const setElement = useCallback((step, element) => {
    setSteps(prev => ({ ...prev, [step]: { ...prev[step], element } }));
  }, []);

  const nextStep = () => {
    if (!active) {
      return;
    }

    const possibleSteps = Object.keys(steps);
    const nextStep = possibleSteps.findIndex(s => s === currentStep) + 1;
    setCurrentStep(possibleSteps[nextStep]);
    setSteps(prev => ({ ...prev, [currentStep]: { ...prev[currentStep], completed: true } }));
  };

  useEffect(() => {
    if (currentStep) {
      const element = steps?.[currentStep]?.element;
      if (element) {
        const theRealElement = element.mapRef?.parentElement || element;
        theRealElement.dataset.tutorial = 'active';
      }
    }

    return () => {
      if (currentStep) {
        const element = steps?.[currentStep]?.element;
        if (element) {
          const theRealElement = element.mapRef?.parentElement || element;
          theRealElement.dataset.tutorial = 'inactive';
        }
      }
    };
  }, [currentStep, steps]);

  const element = steps?.[currentStep]?.element;
  const theRealElement = element?.mapRef?.parentElement || element;

  return (
    <TutorialContext.Provider
      value={{
        currentStep,
        steps,
        setElement,
        nextStep,
        clear: () => setCurrentStep(null),
      }}
    >
      {children}

      {active && currentStep && steps[currentStep].tooltip && (
        <>
          {theRealElement && theRealElement.id && (
            <>
              <ReactTooltip key={theRealElement.id} anchorId={theRealElement.id} clickable style={{ opacity: 1 }} offset={steps[currentStep].offset || 0}>
                <div className="tooltip-container">
                  {data[currentStep]}

                  {steps[currentStep].canLearn && (
                    <button className="btn-show-info" onClick={nextStep}>
                      Aprendi
                    </button>
                  )}
                </div>
              </ReactTooltip>
              <Arrow to={theRealElement} />
            </>
          )}
        </>
      )}
    </TutorialContext.Provider>
  );
};
